import CheckItem from '../../assets/img/check.svg';
import EditTask from '../../assets/img/edit.svg';
import RemoveTask from '../../assets/img/remove.svg';

// completed is a status from tasks object in DB
const Task = ({ id, text, list, onRemove, onEdit, completed, onComplete }) => {
    // return necesary arguments to func into App.js
    const onChangeCheckbox = (e) => {
        onComplete(list.id, id, e.target.checked);
    }

    return (
        // block of tasks
        <div key={id} className="tasks__item">

            {/* checkbox */}
            <div className="checkbox">
                {/* {`task--${item.id}`} => make chackbox work correctly */}
                <input onChange={onChangeCheckbox} checked={completed} type="checkbox" id={`task--${id}`} />
                <label htmlFor={`task--${id}`}><i className='checked'><img src={CheckItem} alt="check" /></i></label>
            </div>

            {/* task text */}
            <p>{text}</p>

            {/* btn to edit/remove task */}
            <i className='icon' onClick={() => onEdit(list.id, { id, text })}><img src={EditTask} alt="edit" /></i>
            <i className='icon' onClick={() => onRemove(list.id, id)}><img src={RemoveTask} alt="edit" /></i>
        </div>
    )
}

export default Task;